import { Container, Grid, Typography } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router';

import GetDetails from '../requests/getDetailsByimdb';


const Details = () =>{
    const params = useParams<any>();
    const [detail, setDetails] = useState<any>({present : {}});
    const [yes, setYes] = useState(false);


    useEffect(()=>{
        // console.log("yes it's running")
        if(params.type === "imdb"){
            GetDetails(params.id)
            .then(data => {
                console.log(data)
                setDetails(data);
            })
        }
    }, []) 

    return (
        <Container>
            <Grid container spacing={3}>
                <Grid item xs={6}>
                    <img src = {detail.present.Poster} />   
                </Grid>
                <Grid item xs={6}>
                <Typography variant = {"h4"} >
                        {detail.present.Title}
                    </Typography>
                    <Typography>
                        Released On: {detail.present.Released}
                    </Typography>
                    <Typography>
                        {detail.present.Director}
                    </Typography>
                    <Typography >
                        Writers : {detail.present.Writer}
                    </Typography>
                    <Typography>
                        Actors : {detail.present.Actors}
                    </Typography>
                    <Typography>
                        Language : {detail.present.Language}
                    </Typography>
                    <Typography >
                        Rating : {detail.present.imdbRating}
                    </Typography>
                    <Typography>
                        Plot: {detail.present.Plot}
                    </Typography>
                </Grid>
            </Grid>
        </Container>
    )
}



export default Details;