import React, { useEffect, useState } from 'react';
import AllData from '../requests/getPost';
import axios from 'axios';
// material ui library
import { makeStyles,Theme } from '@material-ui/core/styles';
import {Avatar, Container, Typography,} from '@material-ui/core';
import {Box} from '@material-ui/core';
import Card from '@material-ui/core/Card';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import IconButton from '@material-ui/core/IconButton';
import {FavoriteBorderRounded, LocationCityTwoTone} from '@material-ui/icons';
import {Share} from '@material-ui/icons';
import { Paper, Grid } from '@material-ui/core'
import Pagination from '@material-ui/lab/Pagination';
import { useHistory, useLocation, useParams } from 'react-router';
import GetmovieByText from '../requests/getSearchbytext';


const useStyles = makeStyles((theme) => ({
    root: {
        display : 'flex',

    },
    content: {

    },
    mainGrid: {
      },
    avatar: {

    },
    card: {
        height: '100%',
        display: 'flex',
        flexDirection: 'column',
      },
      cardMedia: {
        paddingTop: '56.25%', // 16:9
      },
      cardContent: {
        flexGrow: 1,
      },
      cardGrid: {
        paddingTop: theme.spacing(8),
        paddingBottom: theme.spacing(8),
      },
      pagination : {
        padding : "10%",
        width : "80%"
      }
    
  }));


const SearchbyText = (props:any) =>{
    const cardStyles = useStyles();
    const [data, setData] = useState<any>([{}]);
    const [page, setPage] = useState(1);
    const history = useHistory()
    const location = useLocation()
    // const mediaStyles = useSlopeCardMediaStyles();


    const handlePagination = (event: React.ChangeEvent<unknown>, value: number) => {
      setPage(value)
     
    }
    const handleDetailer = (imdbID:string) =>{
      history.push("/details/imdb/"+imdbID);
    }

  
    const searchQueryHandler = () => {
      if(location.search.includes("&")){
        let queries = location.search.substring(1).split("&");
        return "none"
      }
      else{
        let queries = location.search.substring(1).split("=");
        console.log(queries)
        if(queries[0] === "name")
          return queries[1];
        else
          return "batman"
      }
    }

    useEffect(()=>{
      GetmovieByText(searchQueryHandler())
      .then(data => {
        setData(data.Search);
      })

    }, [])

    return(
      <div>
      <Container className={cardStyles.cardGrid} maxWidth="md">
        <Grid container spacing={4}>
            {data.map((card:any) => (
              <Grid item key={card} xs={12} sm={6} md={4}>
                <Card className={cardStyles.card}>
                  <CardMedia
                    className={cardStyles.cardMedia}
                    image= {card.Poster}
                    title= {card.Title}
                    onClick = {() => handleDetailer(card.imdbID)}
                  />
                  <CardContent className={cardStyles.cardContent}>
                    <Typography gutterBottom variant="h5" component="h2">
                      {card.Title}
                    </Typography>
                    <Typography>
                      Year : {card.Year}
                    </Typography>
                  </CardContent>
                </Card>
              </Grid>
            ))}
            </Grid>
            <Grid item xs = {12} className = {cardStyles.pagination}>
              <Pagination count={10}  color="primary" onChange = {handlePagination}/>
            </Grid>
        </Container>
        </div>
    )
}


export default SearchbyText;



